/* Task Table Container */
.task-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    position: relative;
}

/* Task Grid */
.task-grid {
    display: grid;
    width: 85%;
    border-collapse: collapse;
    grid-template-columns: repeat(5, 1fr);
}

/* Task Grid Header */
.task-grid-header {
    display: contents;
}

.task-grid-header > div {
    background-color: #1e1e1e;
    color: #fff;
    padding: 12px;
    border-bottom: 1px solid #555;
    font-weight: bold;
    text-align: left;
}

/* Task Grid Row */
.task-grid-row {
    display: contents;
}

.task-grid-row > div {
    padding: 12px;
    border-bottom: 1px solid #ddd;
    display: flex;
    align-items: center;
}

.task-grid-row:hover {
    background-color: #f5f5f5;
    cursor: pointer;
}

.task-grid-row.overdue > div {
    background-color: #a94442;
    color: #fff;
}

.task-grid-row.completed-row > div {
    text-decoration: line-through;
    color: #999;
}

.task-grid-row.completed-row > div.task-status {
    text-decoration: none;
    color: #388e3c;
}

/* Task Status Styling */
.task-status {
    padding: 6px 12px;
    border-radius: 4px;
    font-weight: bold;
    display: inline-block;
    cursor: pointer;
    white-space: nowrap;
    text-align: center;
}

.task-status.todo {
    background-color: #007bff;
    color: #fff;
}

.task-status.completed {
    background-color: #388e3c;
    color: #fff;
}

/* Sorting Icons */
.task-sort-icons {
    display: flex;
    gap: 16px;
    align-items: center;
    justify-content: flex-end;
}

.task-sort-icons > svg {
    font-size: 24px;
    cursor: pointer;
}

